
/* Common attribues */

.vue-command,
.vue-command--invert {
  font-family: Consolas,
    Monaco,
    'Andale Mono',
    'Ubuntu Mono',
    monospace;

  @mixin clearfix() {

    &:before,
    &:after {
      display: table;
    }

    &:after {
      clear: both;
    }
  }

  overflow-y: hidden;
  overflow-x: hidden;

  .vue-command__bar,
  .vue-command__bar--invert {
    @include clearfix();
    position: inherit;
    padding-left: 10px;
    padding-right: 10px;
    padding-top: 10px;
    padding-bottom: 10px;
    display: flex;
    justify-content: space-between;
    font-family: -apple-system, BlinkMacSystemFont, sans-serif;
  }

  .vue-command__bar__button,
  .vue-command__bar__button--invert {
    display: inline-block;
    border-radius: 100%;

    &:before {
      content: ' ';
      display: block;
      height: 12px;
      width: 12px;
    }

    &:not(:last-child) {
      margin-right: 8px;
    }
  }

  .vue-command__history--invert,
  .vue-command__history {
    overflow: auto;
    word-break: break-all;
    display: block;
    padding: 12px 12px 12px 12px;
    margin: 0;
    white-space: pre-line;
    line-height: 1.33;
    height: 100%;

    /* Provide reasonable default values */
    ul {
      margin: 0;
      padding: 0;
      list-style-type: none;
    }

    input,
    textarea {
      background: none;
      border: none;
      outline: none;
      flex: 1;
      font-size: 1rem;
      width: 100%;
      resize: none;
      overflow: hidden;
    }
  }

  .vue-command__history__entry--fullscreen {
    height: 100%;
  }
}

/* Individual attribues */

.vue-command {
  $seashell: #f1f1f1;

  .vue-command__bar {
    color: $seashell;
    background-color: #111316;
  }

  .vue-command__bar__title {
    color: $seashell;
  }

  .vue-command__bar__button--close {
    background-color: #ff5f58;
  }

  .vue-command__bar__button--minimize {
    background-color: #ffbd2e;
  }

  .vue-command__bar__button--fullscreen {
    background-color: #29ca41;
  }

  .vue-command__history {
    background-color: #111316;
    color: $seashell;

    /* Provide reasonable default values */
    input,
    textarea {
      background: none;
      color: #ffffff;

      &::placeholder {
        color: rgba(255, 255, 255, 0.5);
      }
    }
  }
}

.vue-command--invert {
  $seashell-invert: #0e0e0e;

  .vue-command__bar--invert {
    background-color: #eeece9;
  }

  .vue-command__bar__title--invert {
    color: $seashell-invert;
  }

  .vue-command__bar__button--close--invert {
    background-color: #00a0a7;
  }

  .vue-command__bar__button--minimize--invert {
    background-color: #0042d1;
  }

  .vue-command__bar__button--fullscreen--invert {
    background-color: #d635be;
  }

  .vue-command__history--invert {
    background-color: #eeece9;
    color: $seashell-invert;

    /* Provide reasonable default values */
    input,
    textarea {
      background: none;
      color: #000000;

      &::placeholder {
        color: rgba(0, 0, 0, 0.5);
      }
    }
  }
}
